import React from 'react'

function Thanks() {
    const bgStyle = {
        textAlign: 'center',
        minHeight: '80vh',
        // marginTop: '10%',
        width: '100%',
        backgroundImage: "url(https://thedesignman.com/corona/images/thanks.svg)",
        backgroundColor: '#67BAFE',
        backgroundPosition: 'center',
        backgroundSize: 'contain',
        backgroundRepeat: 'no-repeat'
    }
    return (
        <div style={bgStyle}>

        </div>
    )
}

export default Thanks













