import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Bar } from 'react-chartjs-2';
import { useParams } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '@material-ui/core/CircularProgress';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';


function Details() {
    const [graphCases, setgraphCases] = useState([]);
    const [graphDeaths, setgraphDeaths] = useState([]);
    const [graphRecovered, setgraphRecovered] = useState([]);
    const [country, setCountry] = useState();
    const countryParam = useParams().country;
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetchGraphs(countryParam);
    }, [countryParam]);


    const fetchGraphs = (countryParam) => {
        setLoading(true);
        fetch(`https://disease.sh/v2/historical/${countryParam}?lastdays=60`)
            .then(res => res.json())
            .then((r) => {
                setgraphCases(r.timeline.cases);
                setgraphDeaths(r.timeline.deaths);
                setgraphRecovered(r.timeline.recovered);
                setCountry(r.country);
                setLoading(false);
            });
    }


    const dates = (Object.keys(graphCases));
    const casenum = (Object.values(graphCases));
    const deathsnum = (Object.values(graphDeaths));
    const recoverednum = (Object.values(graphRecovered));
    const data = {
        labels: dates,
        datasets: [
            {
                label: "Cases",
                data: casenum,
                borderColor: "rgba(33, 150, 243, 1)",
                borderWidth: 1,
                backgroundColor: "rgba(33, 150, 243, 0.6)",

            },

            {
                label: "Deaths",
                data: deathsnum,
                borderColor: "rgba(255, 87, 34, 1)",
                borderWidth: 1,
                backgroundColor: "rgba(255, 87, 34, 0.6)"
            },

            {
                label: "Recovered",
                data: recoverednum,
                borderColor: "rgba(0, 191, 165, 1)",
                borderWidth: 1,
                backgroundColor: "rgba(0, 191, 165, 0.6)"
            }
        ]
    }

    const container = {
        minHeight: '80vh',
        width: '100vw',
        display: 'flex',
        flexWrap: 'wrap',
        alignContent: 'center',
        justifyContent: 'center'
    }

    const graphBox = {
        width: '90%',
        marginLeft: 'auto',
        marginRight: 'auto',
        marginTop: '30px'
    }


    if (loading) {
        return (
            <div style={container}>
                <div style={{ display: 'flex', justifyContent: 'center', marginTop: '30vh' }}>
                    <CircularProgress />
                </div>
            </div>)
    };

    return (
        <div style={container}>
            <Link to={"/countries"}>
                <div style={{ alignSelf: 'flex-start' }}>
                    <ArrowBackIosIcon fontSize="small" />
                </div>
            </Link>
            <div>
                {<Typography variant="h5" style={{ textAlign: 'center' }}>Graph for {country} </Typography>}
                {<Typography style={{ textAlign: 'center' }}>Click the legend below to isolate records</Typography>}
            </div>
            <div style={graphBox}>
                <Bar data={data} />
            </div>
        </div>
    )
}

export default Details
