import React from 'react'
import { NavLink } from 'react-router-dom';
import AppBar from '@material-ui/core/AppBar';
import { Typography } from '@material-ui/core';
import { Paper } from '@material-ui/core';
import './header.css';

const headerStyle = {
    backgroundColor: 'rgb(33, 150, 243)',
    display: 'flex',
    flexFlow: 'wrap',
    justifyContent: 'center',
}

const navStyle = {
    width: '200px',
    display: 'flex',
    justifyContent: 'space-between',
    margin: '10px',
}

function Header() {
    return (
        <AppBar position="sticky">
            <Paper style={headerStyle}>
                <Typography variant="h5" style={{ textAlign: 'center', width: '100%', backgroundColor: '#3C245B', fontWeight: '300', padding: '10px' }}>Covid 19 Tracker</Typography>
                <div style={navStyle}>
                    <NavLink to={'/Countries'} activeClassName="active">
                        <span className="material-icons">
                            language
                    </span>
                    </NavLink>
                    <NavLink to={'/Info'} activeClassName="active" >
                        <span className="material-icons">
                            assignment
                    </span>
                    </NavLink>
                    <NavLink to={'/thanks'} activeClassName="active" >
                        <span className="material-icons">
                            favorite
                    </span>
                    </NavLink>
                </div>
            </Paper>
        </AppBar>
    )
}

export default Header
