import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import InsertChartIcon from '@material-ui/icons/InsertChart';

import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import CircularProgress from '@material-ui/core/CircularProgress';
import Input from '@material-ui/core/Input';
import SearchIcon from '@material-ui/icons/Search';
import InputAdornment from '@material-ui/core/InputAdornment';


import './countries.css';



const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0,
    },
    column: {
        flexBasis: '33.33%',
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary,
    },

    margin_r: {
        marginRight: '10px',
    }
}));

function Countries() {
    const [countriesData, setCountriesData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [filterText, setFilterText] = useState('');
    const getData = () => {
        setLoading(true);
        fetch('https://disease.sh/v2/countries')
            .then(res => res.json())
            .then(data => {
                setCountriesData(data);
                setLoading(false);
            });
    }

    useEffect(() => {
        getData();
    }, []);

    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(false);

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    const searchCountry = (e) => {
        setFilterText(e.target.value.toLowerCase());
    }

    const container = {
        minHeight: '95vh',
        width: '95vw',
        margin: 'auto',
    }


    if (loading) {
        return (
            <div style={container}>
                <div style={{ display: 'flex', justifyContent: 'center', marginTop: '30vh' }}>
                    <CircularProgress />
                </div>
            </div>)
    };
    return (
        <>
            <div style={{ margin: '0 auto 20px', width: '90vw', display: 'flex', alignItems: 'center' }}>
                <Input startAdornment={
                    <InputAdornment position="start">
                        <SearchIcon />
                    </InputAdornment>
                } placeholder="Type name of country to search" onKeyUp={searchCountry} style={{ width: '90vw', padding: '20px' }} />
            </div>
            <div className='stats'>
                {countriesData
                    .filter(x => x.country.toLowerCase().includes(filterText))
                    .map(res =>
                        <ExpansionPanel expanded={expanded === res.country} onChange={handleChange(res.country)} key={res.country}>
                            <ExpansionPanelSummary
                                expandIcon={<ExpandMoreIcon />}
                                aria-controls="panel1bh-content"
                                id="panel1bh-header"
                            >
                                <div className={classes.column}>
                                    <Typography className={classes.heading}>{res.country}</Typography>
                                </div>
                                <div className={classes.column}>
                                    <img width="30px" src={res.countryInfo.flag} alt="Flags" className={classes.margin_r}></img>
                        Population: {res.population}
                                </div>
                            </ExpansionPanelSummary>

                            <ExpansionPanelDetails>
                                <Typography style={{ fontWeight: '300' }}>
                                    Cases: {res.cases} <span>|</span> Recovered: {res.recovered} <span>|</span> Deaths: {res.deaths} <span>|</span> Deaths per Million: {res.deathsPerOneMillion} <span>|</span>
                            Today's Cases: {res.todayCases} <span>|</span> Today's Deaths: {res.todayDeaths} <span>|</span> Tests per million: {res.testsPerOneMillion}
                                </Typography>
                            </ExpansionPanelDetails>
                            <Divider />
                            <ExpansionPanelActions>
                                <Link to={'/details/' + res.country}>
                                    <Button size="small" variant="contained" color="primary" className={classes.button}
                                        endIcon={<InsertChartIcon />}>
                                        Chart
                            </Button>
                                </Link>
                            </ExpansionPanelActions>
                        </ExpansionPanel>

                    )
                }
            </div >
        </>
    )
}

export default Countries
