import React from 'react';

const nameStyle = {
    fontSize: 15,
    textAlign: 'center',
    paddingTop: 20,
};

const centerText = {
    fontSize: 12,
    fontWeight: 300,
    textAlign: "center"
}


function Footer() {
    return (
        <div style={{ backgroundColor: '#000', paddingBottom: 20 }}>
            <div style={nameStyle}>Adi Gonsalves</div>
            <div style={centerText}>thedesignman.com</div>
            <div style={centerText}>Built with React, Material UI and Chart JS</div>
            <div style={centerText}>Data provided by NovelCOVID API</div>
            < a href="https://www.freepik.com/free-photos-vectors/infographic">
                <p style={centerText}> Infographic vector created by pikisuperstar - www.freepik.com</p>
            </a >
        </div>
    )
}

export default Footer
// <a href="https://www.freepik.com/free-photos-vectors/infographic">Infographic vector created by freepik - www.freepik.com</a>