import React from 'react';
const bgStyle = {
    textAlign: 'center',
    minHeight: '80vh',
    // marginTop: '10%',
    width: '100%',
    backgroundImage: "url(https://thedesignman.com/corona/images/corona1.svg)",
    backgroundColor: '#3C245B',
    backgroundPosition: 'center',
    backgroundSize: 'contain',
    backgroundRepeat: 'no-repeat'
}

function Info() {
    return (
        <div style={bgStyle}>

        </div>
    )
}

export default Info
